.search {
    position: absolute;
    top: 35px;
    left: 25%;
    width: 100vw;
}

.search > h2 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 559px;
    padding: 10px;
    background-color: white;
    position: absolute;
    left: 0;
    top: 327px;
}

.search > input {
    width: 559px;
    padding: 20px;
    position: absolute;
    left: 0px;
    height: 30px;
    top: 379px;
    border: none;
}
  
.search > input:focus {
    outline-width: 0;
}

.search > button {
    position: absolute;
    left: -2px;
    top: 419px;
    text-transform: inherit !important;
    background-color: #ff7779;
    color: white;
    width: 562px;
}

.search > button:hover {
    background-color: rgb(203, 20, 20);
    color: #ff7779;
}